











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class MenuItem extends Vue {
    public hasChildMenus = false;

    @Prop()
    public routePath!: string|null;

    public get menuItemClass()
    {
        return {
            'animenu__nav__hasDropdown': this.hasChildMenus,
            'animenu__selected': this.routePath == this.$route.path
        };
    }

    public mounted()
    {
        this.hasChildMenus = !!this.$slots.default;
    }

    public menuItemClicked()
    {
        const path = this.routePath;
        if (path)
        {
            this.$router.push(path);
        }
    }
}
